.hero {
  padding: 20vh 3.75rem 15vh 3.75rem;
}

.hero-wrapper {
  max-width: var(--max-width-wrapper);
  margin: 0 auto;
}

/* 
  ==============================================
  HERO-TOP-ROLE === LAYOUT
  ==============================================
*/

.hero-top-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 2.7rem;
}

.hero-top-role {
  width: 50%;
}

.hero-top-fname {
  width: 50%;
}

/* 
  ==============================================
  HERO-TOP-ROLE === TYPOGRAPH
  ==============================================
  *** .hero-top-fname > h1 : First name
  *** .hero-top-role > h2 : The Job Title
  *** .hero-top-role > p : Description
*/

.hero-top-fname > h1 {
  font-size: var(--fs-header-1);
  font-weight: 500;
  text-transform: uppercase;

  text-align: right;

  background: var(--linear-bg-title);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

.hero-top-role > h2 {
  color: #a3f491;
  font-size: var(--fs-header-3);
  font-weight: 600;
  margin-bottom: 0.8rem;
}

.hero-top-role > p {
  color: #fff;
  font-size: var(--fs-para-3);
  font-weight: 500;
  max-width: 25em;
}

.hero-cta-container {
  margin-top: 3rem;

  color: #fff;

  font-weight: 500;
  font-size: 1.3rem;
  letter-spacing: 1px;
}

.hero-cta-container > a {
  padding: 1rem;
  border-radius: 50px;

  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.hero-cta-container > a:hover  {
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out !important;

  background-color: #fff;
  color: #000000;
}

.hero-cta-container > a >  img {
  padding-left: 0.7rem;
}

.hero-cta-container > a >  img:hover {
  fill: #000;
}

.hero-cta-arrow {
  display: none;

  moz-transition: all .6s ease-in-out;
  -o-transition: all .6s ease-in-out;
  -webkit-transition: all .6s ease-in-out;
  transition: all .6s ease-in-out;
}

.hero-cta-arrow--active {
  moz-transition: all .6s ease-in-out;
  -o-transition: all .6s ease-in-out;
  -webkit-transition: all .6s ease-in-out;
  transition: all .6s ease-in-out;

  display: inline-block;

}

/* 
  ==============================================
  HERO-BOTTOM-ROLE
  ==============================================
  *** .hero-bottom-container > h2 : Last name
*/

.hero-bottom-container > h2 {
  font-size: var(--fs-header-1);
  font-weight: 500;
  text-transform: uppercase;

  background-image: var(--linear-bg-title);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

@media (max-width: 1000px) {
  .hero {
    padding: 20vh 1rem 10vh 1rem;
  }

  .hero-top-container {
    flex-direction: column;
    gap: 3rem;
    margin: 0;
  }

  .hero-top-role,
  .hero-top-fname {
    width: 100%;
  }

  .hero-top-fname > h1, .hero-bottom-container > h2 {
    text-align: left;
  }
}
